<template>
    <main class="app-main">
        <section class="top" style="position: absolute!important;">
            <p class="title">个人中心</p>
        </section>
        <section>
            <div class="top-content">
                <router-link class="head-portrait" to="/personal/checkInfo">
                    <img style="width: 64px;height: 64px" :src="userImg" alt="">
                </router-link>
                <!--<router-link  v-if="!isLogin" class="head-portrait" to="/personal/checkInfo">-->
                    <!--<img style="width: 64px;height: 64px" src="../../assets/personal/5.png" alt="">-->
                <!--</router-link>-->
                <!--<div class="login-btn" v-if="!isLogin">-->
                    <!--<router-link tag="span" to="login"><span>登录/</span></router-link><router-link tag="span" to="/register"><span>注册</span></router-link>-->
                <!--</div>-->
                <div class="login-btn">
                    <span>{{ userPhone }}</span>
                    <!--<span>{{ userPhone }}</span>-->
                </div>
            </div>
        </section>
        <div class="con-top">
            <div class="menu">
                <ul>
                    <router-link to="/personal/release" tag="li">
                        <div>
                            <img src="@/assets/personal/1.png" alt="">
                        </div>
                        <span>我的发布</span>
                    </router-link>
                    <router-link to="/personal/activity" tag="li">
                        <div>
                            <img src="@/assets/personal/2.png" alt="">
                        </div>
                        <span>我的活动</span>
                    </router-link>
                    <router-link to="/personal/project" tag="li">
                        <div>
                            <img src="@/assets/personal/3.png" alt="">
                        </div>
                        <span>我的项目</span>
                    </router-link>
                    <router-link to="/personal/collect" tag="li">
                        <div>
                            <img src="@/assets/personal/4.png" alt="">
                        </div>
                        <span>我的收藏</span>
                    </router-link>
                </ul>
            </div>
        </div>
        <div class="con-con">
            <router-link v-if="this.$store.getters.userInfo.roleId === 1" to="/personal/cert">
                <md-cell-item title="我的认证" brief="" addon="" arrow>
                    <span class="holder" slot="left"><img style="width: 20px" src="../../assets/personal/11.png" alt=""></span>
                </md-cell-item>
            </router-link>
            <router-link v-if="this.$store.getters.userInfo.roleId !== 1" to="/personal/entCert">
                <md-cell-item title="我的认证" brief="" addon="" arrow>
                    <span class="holder" slot="left"><img style="width: 20px" src="../../assets/personal/11.png" alt=""></span>
                </md-cell-item>
            </router-link>
            <router-link to="/personal/server" v-if="!isLogin">
                <md-cell-item title="我的服务" brief="" addon="" style="border-bottom: 1px solid rgba(153,153,153,0.1);" arrow>
                    <span class="holder" slot="left"><img style="width: 20px" src="../../assets/personal/17.png" alt=""></span>
                </md-cell-item>
            </router-link>
            <router-link to="/personal/Docking">
                <md-cell-item title="我的对接" arrow style="margin-bottom: 20px">
                    <span class="holder" slot="left"><img style="width: 20px" src="../../assets/personal/12.png" alt=""></span>
                </md-cell-item>
            </router-link>
        </div>
        <div class="con-con">
            <router-link to="/personal/message">
                <md-cell-item title="系统消息" brief="" addon="" style="border-bottom: 1px solid rgba(153,153,153,0.1);" arrow>
                    <span class="holder" slot="left"><img src="../../assets/personal/16.png" alt=""></span>
                </md-cell-item>
            </router-link>
            <router-link to="/personal/member">
                <md-cell-item title="会员权益" brief="" addon="" arrow>
                    <span class="holder" slot="left"><img src="../../assets/personal/15.png" alt=""></span>
                </md-cell-item>
            </router-link>
            <router-link to="/personal/Feedback">
                <md-cell-item title="意见反馈" brief="" addon="" style="border-bottom: 1px solid rgba(153,153,153,0.1);" arrow>
                    <span class="holder" slot="left"><img src="../../assets/personal/13.png" alt=""></span>
                </md-cell-item>
            </router-link>
            <router-link to="/personal/Code">
                <md-cell-item title="我的邀请码" brief="" addon="" arrow>
                    <span class="holder" slot="left"><img src="../../assets/personal/14.png" alt=""></span>
                </md-cell-item>
            </router-link>
        </div>
        <input class="logout" type="button" value="退出" @click="logout">
    </main>
</template>

<script>
import { Field, CellItem, Icon, Toast, Dialog } from 'mand-mobile'
import { getInfo } from '../../apiUrl/personal/info'
import { removeToken } from '@/utils/auth'
import { mapGetters } from 'vuex'

export default {
  name: 'index',
  components: {
    [Dialog.name]: Dialog,
    [Field.name]: Field,
    [CellItem.name]: CellItem,
    [Icon.name]: Icon,
    [Toast.name]: Toast
  },
  data () {
    return {
      isLogin: false,
      userImg: '',
      userPhone: ''
    }
  },
  computed: {
    ...mapGetters([
      'token'
    ])
  },
  mounted () {
    this.getInfo()
    // this.isLogin = this.$store.state.user.status
    this.$store.state.user.userInfo.roleId === 1 ? this.isLogin = true : this.isLogin = false
  },
  methods: {
    getInfo () {
      getInfo().then((res) => {
        this.userImg = res.data.img
        this.userPhone = res.data.userName
        if (res.data.img) {
          this.userImg = res.data.img
        } else {
          this.userImg = require('../../assets/personal/5.png')
        }
      }).catch(err => {
        Toast.failed(err)
      })
    },
    logout () {
      Dialog.confirm({
        title: '提示',
        content: '确认退出？',
        cancelText: '取消',
        confirmText: '确定',
        onConfirm: () => {
          this.$store.dispatch('Logout')
          removeToken()
        }
      })
    }
    // getDictionary() {
    //     getDictionary({ type: 'academic' }).then((e) => {
    //         e.data.forEach(e => {
    //             e.value==this.$store.getters.
    //         })
    //     })
    // }
  }
}
</script>

<style lang="stylus" scoped>
.top-content
    padding 50px
    padding-top 88px
    background url("../../assets/personal/bg.png") no-repeat
    overflow hidden
    .head-portrait
        width:160px;
        height:160px;
        float left
        background-size 100%
        border:1px solid rgba(102, 102, 102, 0.15);
        border-radius:50%;
    .login-btn
        margin-left 33px
        float left
        font-size 40px
        padding-top 61px
        color #fff
.top
    .title
        background none
.con-top
    width 90%
    margin -80px auto
    height: 200px
    margin-bottom 20px
    background:rgba(255,255,255,1);
    box-shadow:0px 4px 24px 0px rgba(2,25,85,0.1);
    border-radius:8px;
    .menu
        margin 50px 30px
        font-size 20px
        padding-top 10px
        border-radius 8px
        ul
            display flex
            flex-wrap wrap
            li
                flex 0 0 25%
                display flex
                align-items center
                justify-content flex-end
                height 170px
                flex-direction column
                div
                    flex 1
                    display flex
                    justify-items center
                    align-items center
                    img
                        width 50px
                span
                    font-size 24px
                    margin-bottom 20px
.con-con
    width 90%
    margin 0px auto
    background:rgba(255,255,255,1);
    box-shadow:0px 4px 24px 0px rgba(2,25,85,0.1);
    border-radius:12px;
    .holder
        display flex
        img
            width 35px
            height 32px
/deep/.md-cell-item-body
    justify-content space-between!important
    padding 0px 20px
    text-align left
/deep/.md-cell-item-content
    flex: 1 1 0%!important
/deep/.md-cell-item-left
    margin-right 10px
/deep/.md-cell-item
    height 91px
/deep/.md-cell-item-body:before
    top:91px
    bottom auto
.logout
    width 660px
    height 88px
    color #ffffff
    background-color #021955FF
    border 0
    background rgba(2,25,85,1)
    box-shadow 0px 4px 24px 0px rgba(2,25,85,0.1)
    border-radius 12px
    font-size 32px
    margin-bottom 60px
.app-main
    padding-bottom 160px
</style>
